.i-block {
  background-image: url(../images/production_1.jpg);
  background-repeat: no-repeat;
  background-position: right;
  background-size: auto 100%;
  position: relative;
  margin-bottom: 25px;
  @media (max-width: 768px) {
    background-position: center;
    background-size: cover;
  }
  @media (max-width: 768px) {
    margin-bottom: 10px;
  }
  &__text-wrap {
    width: 60%;
    padding-right: 60px;
    box-sizing: border-box;
    background-image: linear-gradient(to right, #fff 85%, transparent);
    @media (max-width: 768px) {
      width: 100%;
      background: rgba(255, 255, 255, 0.7);
      padding-right: 0;
    }
  }
  &__text {
    padding: 71px 84px;
    @media (max-width: 1024px) {
      padding-left: 10px;
    }
    @media (max-width: 460px) {
      padding: 50px 10px;
    }
  }
  &__title {
    color: #14191f;
    font-size: 36px;
    text-transform: uppercase;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 35px;
    span {
      display: block;
    }
    @media (max-width: 1024px) {
      font-size: 30px;
      margin-bottom: 25px;
    }
    @media (max-width: 460px) {
      font-size: 26px;
      margin-bottom: 22px;
    }
  }
  &__description {
    color: #404040;
    font-size: 14px;
    line-height: 21px;
    @media (max-width: 460px) {
      line-height: 17px;
    }
  }
  &__name {
    position: absolute;
    bottom: 35px;
    right: 33px;
    color: #ffffff;
    font-size: 18px;
    text-shadow: 0 3px 6px rgba(0, 0, 0, 1);
    @media (max-width: 1024px) {
      font-size: 14px;
      bottom: 10px;
      right: 10px;
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
}

.block-photo {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
  @media (max-width: 440px) {
    flex-wrap: wrap;
  }
  &__item {
    width: 31.8%;
    img {
      width: 100%;
    }
    @media (max-width: 768px) {
      width: 33%;
    }
    @media (max-width: 440px) {
      width: 100%;
      padding: 0 10px;
      box-sizing: border-box;
      margin-bottom: 4px;
    }
  }
}

.print-main {
  background-image: url(../images/print_bg.jpg);
  background-repeat: no-repeat;
  background-position: right;
  background-size: auto 100%;
  background-color: #fff;
  min-height: 395px;
  @media (max-width: 960px) {
    min-height: 300px;
  }
  &__text-wrap {
    width: 33%;
    padding: 100px 80px;
    @media (max-width: 1120px) {
      width: 50%;
    }
    @media (max-width: 1024px) {
      padding: 100px 10px;
    }
    @media (max-width: 960px) {
      width: 100%;
      background-color: rgba(255, 255, 255, 0.7);
      height: 300px;
      box-sizing: border-box;
    }
    @media (max-width: 560px) {
      padding-top: 50px;
    }
  }
  &__title {
    color: #3f9c41;
    font-size: 36px;
    @media (max-width: 460px) {
      font-size: 30px;
    }
  }
}

.print-blocks {
  display: flex;
  justify-content: space-between;
  padding: 100px 0 140px 0;
  background-image: url(../images/print-blocks_bg.png);
  background-position: center;
  background-repeat: no-repeat;
  @media (max-width: 1260px) {
    padding: 100px 10px 140px 10px;
  }
  @media (max-width: 1024px) {
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 340px;
    background-position: center bottom;
  }
  &__item {
    width: 321px;
    @media (max-width: 1024px) {
      margin-bottom: 30px;
      width: 500px;
    }
    @media (max-width: 520px) {
      width: 100%;
    }
  }
  &__title {
    color: #3f9c41;
    font-size: 36px;
    text-transform: uppercase;
    @media (max-width: 768px) {
      font-size: 30px;
    }
    @media (max-width: 460px) {
      font-size: 22px;
      margin-bottom: 15px;
    }
  }
  &__description {
    font-size: 14px;
    line-height: 21px;
    color: #404040;
  }
}

.technologies-main {
  height: 395px;
  background-image: url(../images/technologies-main.png);
  background-repeat: no-repeat;
  background-position: top right;
  background-color: #fff;
  display: flex;
  align-items: center;
  @media (max-width: 960px) {
    background-position: top right -200px;
  }
  @media (max-width: 768px) {
    display: block;
  }
  @media (max-width: 460px) {
    height: 300px;
  }
  &__text-wrap {
    padding: 20px 80px;
    @media (max-width: 768px) {
      padding: 20px 10px;
      background-color: rgba(255, 255, 255, 0.4);
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      display: flex;
      align-items: center;
    }
  }
  &__title {
    font-size: 36px;
    text-transform: uppercase;
    color: #14191f;
    font-weight: 700;
    margin-top: 0;
    @media (max-width: 460px) {
      font-size: 30px;
    }
    span {
      display: block;
    }
  }
}

.mechanism {
  margin: 50px 0;
  display: flex;
  align-items: center;
  @media (max-width: 768px) {
    display: block;
  }
  &__img {
    min-width: 600px;
    @media (max-width: 1024px) {
      min-width: 400px;
    }
    @media (max-width: 768px) {
      min-width: 0;
      width: 100%;
    }
  }
  &__text-wrap {
    padding: 20px 120px;
    width: 50%;
    box-sizing: border-box;
    @media (max-width: 1024px) {
      padding: 20px 40px;
    }
    @media (max-width: 768px) {
      width: 100%;
    }
  }
  &__title {
    font-size: 24px;
    @media (max-width: 460px) {
      font-size: 20px;
    }
  }
  &__description {
    font-size: 14px;
  }
}

.lite-blocks {
  background-color: #fff;
  padding: 40px 0;
  @media (max-width: 480px) {
    padding: 10px 0 20px 0;
  }
  &__wrap {
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    width: 33%;
    padding: 45px 40px;
    box-sizing: border-box;
    border-right: 1px solid #d6d6d6;
    border-top: 1px solid #d6d6d6;
    &:nth-child(3n) {
      border-right: none;
    }
    &:nth-child(1), &:nth-child(2), &:nth-child(3) {
      border-top: none;
    }
    @media (max-width: 768px) {
      width: 50%;
      &:nth-child(3n) {
        border-right: 1px solid #d6d6d6;
      }
      &:nth-child(3) {
        border-top: 1px solid #d6d6d6;
      }
    }
    @media (max-width: 480px) {
      width: 100%;
      border-right: none;
      &:nth-child(2) {
        border-top: 1px solid #d6d6d6;
      }
    }
  }
  &__title {
    font-size: 16px;
    text-transform: uppercase;
    margin-bottom: 24px;
    margin-top: 0;
  }
  &__description {
    font-size: 14px;
    opacity: 0.84;
  }
}

.long-advantages {
  padding: 50px 0;
  display: flex;
  @media (max-width: 1260px) {
    padding: 50px 10px;
  }
  @media (max-width: 960px) {
    flex-wrap: wrap;
  }
  &__item {
    width: 20%;
    padding-right: 60px;
    box-sizing: border-box;
    padding-top: 120px;
    position: relative;
    @media (max-width: 1260px) {
      padding-right: 40px;
    }
    @media (max-width: 960px) {
      width: 33%;
      margin-bottom: 40px;
    }
    @media (max-width: 580px) {
      width: 50%;
    }
    @media (max-width: 460px) {
      width: 100%;
      padding-top: 100px;
      padding-left: 30px;
    }
    &:before {
      content: '';
      display: block;
      background-image: url(../images/sprite-tech.png);
      position: absolute;
      top: 20px;
    }
    &:nth-child(1):before {
      background-position: 0px 0px;
      width: 55px;
      height: 55px;
    }
    &:nth-child(2):before {
      background-position: -57px 0px;
      width: 52px;
      height: 52px;
    }
    &:nth-child(3):before {
      background-position: 0px -57px;
      width: 48px;
      height: 52px;
    }
    &:nth-child(4):before {
      background-position: -111px 0px;
      width: 44px;
      height: 49px;
    }
    &:nth-child(5):before {
      background-position: -50px -57px;
      width: 48px;
      height: 46px;
    }
  }
  &__title {
    font-size: 18px;
    margin-bottom: 22px;
    margin-top: 0;
    @media (max-width: 460px) {
      margin-bottom: 8px;
    }
  }
  &__description {
    opacity: 0.79;
    font-size: 12px;
    line-height: 18px;
  }
}
